.cart {
    padding: 72px 0 80px;

    &__begin {
        display: flex;
        gap: 14px;
        align-items: center;
        margin-bottom: 14px;
    }

    &__bg {
        background-color: $accent-color-100;
        padding: 10px;
        border-radius: 50%;
        line-height: 0;
    }

    &__icon {
        fill: $light-color-100;
    }

    &__title {
        color: $dark-text-100;
        font-size: 24px;
        font-weight: 500;
        line-height: 1.17;
        text-transform: uppercase;
    }

    &__span {}

    &__body {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &__left {
        width: 100%;
    }

    &__del {
        display: flex;
        gap: 8px;
        align-items: center;
        color: $dark-text-100;
        font-size: 14px;
        font-weight: 500;
        line-height: 1.29;
        background-color: transparent;
        margin: 0 0 40px auto;
        padding: 0;
    }

    &__dagger {
        fill: $dark-text-100;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    &__item {
        display: flex;
        gap: 20px;
        padding-top: 30px;
        border-top: 1px $dark-text-10 solid;

        &:first {
            padding-top: 0;
            border-top: none;
        }
    }

    &__close {
        position: absolute;
        top: 22px;
        right: 7px;
    }

    &__wrapper {}

    &__img {}

    &__text {}

    &__header {}

    &__infos {}

    &__info {}

    &__value {}

    &__dollars {}

    &__cost {}

    &__form {}

    &__subtitle {}

    &__prise {}

    &__total {}

    &__sum {}

    &__num {}

    &__footer {}

    &__label {}

    &__input {}

    &__sub {}
}