a {
	text-decoration: none;
	outline: none;
}

button {
	outline: none;
	border: none;
	cursor: pointer;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

input {
	outline: none;
}

body {
	margin: 0;
}